.article-teaser {
	text-align: center;
	margin: 0 auto;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.article-teaser .teaser-img {
	position: relative;
}

.article-teaser .teaser-img a:hover img {
	opacity: .9;
}

.article-teaser .teaser-img img {
	max-height: 240px;
	max-height: 240px;
	object-fit: cover;
	-o-object-fit: cover;
	object-fit: cover;
	min-height: 240px;
}

.sticky {
	width: 100%;
	position: relative;
}

.sticky .entry-header {
	position: absolute;
	left: 50%;
	top: 15%;
	padding: 30px 60px 30px;
	transform: translateX(-50%);
	z-index: 1;
	border: 2px solid white;
	transition: all .3s ease;
}

@media (min-width: 700px) {
	.sticky .entry-header {
		top: 35%;
	}
}

.sticky .entry-header a, .sticky .entry-header .category {
	transition: all .3s ease;
	color: #fff;
}

.sticky .entry-header:hover {
	transition: all .3s ease;
	background-color: #fff;
}

.sticky .entry-header:hover a, .sticky .entry-header:hover .category {
	transition: all .3s ease;
	color: #222;
}

.sticky .teaser-img {
	position: relative;
}

.sticky .teaser-img img {
	max-height: 390px;
	object-fit: cover;
}

.sticky .teaser-img:after {
	content: '';
	background-color: rgba(0, 0, 0, 0.6);
	position: absolute;
	top: 0;
	width: 100%;
	height: calc(100% - 9px);
	left: 0;
}

/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.site-header {
	position: relative;
	font-family: "Lora", serif;
	margin-top: 20px;
	color: #fff;
	background-color: #fff;
	padding: 0 2em;
}

.site-header h1, .site-header p {
	margin: 0 0 10px 0;
}

.site-header h1:hover, .site-header p:hover {
	text-decoration: none;
}

@media (min-width: 700px) {
	.site-header {
		margin-top: 50px;
	}
}

.site-branding {
	min-height: 65px;
}

.site-title {
	text-align: center;
	margin-bottom: 30px;
	margin: 0;
	padding: 0;
	font-weight: 400;
	line-height: 1em;
}

.site-title h1 {
	font-size: 1.4em;
	margin-bottom: 20px;
}

@media (min-width: 700px) {
	.site-title {
		text-align: left;
		margin-bottom: 20px;
	}
	.site-title h1 {
		margin-bottom: 20px;
		font-size: 1.6em;
	}
}

.site-title a {
	color: #556b2f;
	text-decoration: none;
}

.site-title a:hover, .site-title a:focus {
	text-decoration: underline;
}

.site-title a#header-phone {
	font-size: .6rem;
	font-family: "Heebo", sans-serif;
	letter-spacing: 1px;
	padding: 3px 8px;
	border: 1px solid #222;
	color: #222;
	margin-top: 20px;
}

.site-description {
	margin: 0;
	font-size: .9em;
	font-style: italic;
	font-weight: 100;
	color: #556b2f;
}

/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
	display: block;
}

.hentry {
	margin: 0 0 1.5em;
}

.updated:not(.published) {
	display: none;
}

.entry-title a,
.category a {
	text-decoration: none;
}

.entry-title a:hover,
.category a:hover {
	text-decoration: underline;
}

.category-link {
	font-size: .55em;
	margin-bottom: .5em;
	letter-spacing: 1px;
	text-transform: uppercase;
}

.category-link a {
	color: #aaa;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.nav-links a {
	padding: 20px;
	border: 2px solid #222;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
footer {
	background: #eee;
}

footer .site-info {
	padding: 1em;
	max-width: 1100px;
	margin: 0 auto;
	display: flex;
	justify-content: center;
	flex-direction: column;
}

@media (min-width: 700px) {
	footer .site-info {
		padding: 2em 2em 0;
		flex-direction: row;
		justify-content: space-between;
	}
}

footer .site-info #text {
	margin-left: 20px;
}

footer .site-info #footer-cta {
	margin-left: 0px;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 40px;
	font-family: "Lora", serif;
}

footer .site-info #footer-cta a {
	text-decoration: none;
	padding: 10px;
	color: #ba6803;
	font-size: 1.2rem;
}

@media (min-width: 700px) {
	footer .site-info #footer-cta {
		height: 70px;
		margin-left: 30px;
		background-color: #556b2f;
	}
	footer .site-info #footer-cta a {
		color: #fff;
		padding: 20px;
		font-size: .8rem;
	}
}

footer .site-info #arin-text {
	text-align: center;
	margin-bottom: 30px;
}

@media (min-width: 700px) {
	footer .site-info #arin-text {
		text-align: left;
		margin-bottom: 10px;
	}
}

footer .site-info #garcia-text {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-bottom: 30px;
}

@media (min-width: 700px) {
	footer .site-info #garcia-text {
		flex-direction: row;
		margin-bottom: 10px;
	}
}

footer .site-info #garcia-text img {
	max-height: 70px;
	margin-right: 20px;
}

footer #lincoln p {
	text-align: center;
	margin-bottom: 10px;
	font-size: .6em;
	color: #aaa;
}

footer #lincoln a {
	color: #aaa;
}

footer p {
	margin: 0;
	font-size: .7rem;
	color: #556b2f;
}

footer a {
	color: #556b2f;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}

.hero {
	height: 250px;
	background-size: cover;
}

@media (min-width: 700px) {
	.hero {
		height: 600px;
	}
}
